
Template.__checkName("Navbar");
Template["Navbar"] = new Template("Template.Navbar", (function() {
  var view = this;
  return [ HTML.DIV({
    "class": "navbar-fixed"
  }, "\n		", HTML.NAV({
    "class": "purple lighten-2 z-depth-0"
  }, "\n			", HTML.DIV({
    "class": "nav-wrapper container"
  }, "\n				", HTML.Raw('<a href="/" class="brand-logo left">Empanist</a>'), "\n				", HTML.UL({
    id: "nav-mobile",
    "class": "right valign-wrapper hide-on-med-and-down"
  }, "\n						", Blaze.Each(function() {
    return Spacebars.call(view.lookup("navbarFields"));
  }, function() {
    return [ "\n							", HTML.LI(Blaze._TemplateWith(function() {
      return {
        template: Spacebars.call(view.lookup("."))
      };
    }, function() {
      return Spacebars.include(function() {
        return Spacebars.call(Template.__dynamic);
      });
    })), "\n						" ];
  }), "\n				"), "\n			"), "\n		"), "\n	"), HTML.Raw("\n\n<!-- Modal Contents -->\n\n  "), HTML.DIV({
    id: "signUp",
    "class": "modal"
  }, "\n    ", HTML.DIV({
    "class": "modal-content"
  }, "\n      ", Spacebars.include(view.lookupTemplate("atForm")), "\n    "), "\n  "), "\n\n  ", HTML.DIV({
    id: "login",
    "class": "modal"
  }, "\n    ", HTML.DIV({
    "class": "modal-content"
  }, "\n      ", Spacebars.include(view.lookupTemplate("atForm")), "\n    "), "\n  ") ];
}));

