Template.__checkName("navbarAccount");
Template["navbarAccount"] = new Template("Template.navbarAccount", (function() {
  var view = this;
  return [ HTML.A({
    "class": "dropdown-button valign-wrapper",
    "data-activates": "accountDropdown"
  }, "Account", HTML.Raw("&nbsp;"), HTML.Raw("&nbsp;"), HTML.Raw("&nbsp;"), HTML.Raw("&nbsp;"), HTML.IMG({
    src: function() {
      return Spacebars.mustache(view.lookup("profilePic"));
    },
    alt: "",
    width: "37px",
    height: "37px",
    style: "border:2px solid white",
    "class": "circle right valign white-border"
  }), "\n		"), "\n\n  ", HTML.UL({
    id: "accountDropdown",
    "class": "dropdown-content",
    style: "width: auto !important;"
  }, "\n    ", HTML.LI(HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("getProfileRoute"));
    }
  }, "My Profile")), "\n    ", HTML.Raw('<li class="divider"></li>'), "\n    ", HTML.Raw('<li><a class="logout">Logout</a></li>'), "\n  ") ];
}));

