
Template.__checkName("ResultsLayout");
Template["ResultsLayout"] = new Template("Template.ResultsLayout", (function() {
  var view = this;
  return [ HTML.Raw("<header>\n        <h1> Search Results </h1>\n    </header>\n    "), HTML.MAIN("\n		", Spacebars.include(view.lookupTemplate("advancedSearch")), "\n		", Spacebars.include(view.lookupTemplate("results")), "\n    ") ];
}));

Template.__checkName("results");
Template["results"] = new Template("Template.results", (function() {
  var view = this;
  return Blaze.Each(function() {
    return Spacebars.call(view.lookup("accompanists"));
  }, function() {
    return [ "\n\n		", HTML.H5("Name: ", Blaze.View("lookup:accompname.name", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("accompname"), "name"));
    })), "\n		", HTML.H5("Affiliation: ", Blaze.View("lookup:accompname.affiliation", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("accompname"), "affiliation"));
    })), "\n		\n		", HTML.H5("Session Location: ", Blaze.View("lookup:mylocation", function() {
      return Spacebars.mustache(view.lookup("mylocation"));
    })), "\n		", HTML.H5("Charge: $", Blaze.View("lookup:charge", function() {
      return Spacebars.mustache(view.lookup("charge"));
    }), "/Hour"), "\n		", HTML.H5("Session Time Pref: ", Blaze.View("lookup:working_hours", function() {
      return Spacebars.mustache(view.lookup("working_hours"));
    })), "\n		", HTML.H5("Working Days: ", Blaze.View("lookup:working_days", function() {
      return Spacebars.mustache(view.lookup("working_days"));
    })), "\n		", HTML.H5("About: ", Blaze.View("lookup:one_liner", function() {
      return Spacebars.mustache(view.lookup("one_liner"));
    })), "\n		", HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("getProfileRoute"), view.lookup("Id"));
      }
    }, "View Profile"), "\n		", HTML.HR(), "\n	" ];
  });
}));

Template.__checkName("advancedSearch");
Template["advancedSearch"] = new Template("Template.advancedSearch", (function() {
  var view = this;
  return HTML.DIV({
    "class": "container"
  }, "\n		", HTML.DIV({
    "class": "row"
  }, "\n	        ", HTML.DIV({
    "class": "span12"
  }, "\n	            ", HTML.getTag("h8")("Advanced Search"), "\n	            ", HTML.Raw('<form id="searchform">\n	            	<!-- make it auto complete city for user -->\n  					<input name="address" class="autocomplete" id="autocomplete" placeholder="Session location" type="text">\n	               <!-- add 30min drive raduis button -->\n	                <input name="start_date" class="span5" type="date" placeholder="First day of session">\n\n	               	<input name="end_date" class="span5" type="date" placeholder="Event day">\n\n	                <button type="submit" class="btn btn-primary"> Search</button>\n	            </form>'), "\n	        "), "\n	    "), "\n	");
}));
