
Template.__checkName("AccompanistDashboard");
Template["AccompanistDashboard"] = new Template("Template.AccompanistDashboard", (function() {
  var view = this;
  return [ HTML.Raw("<header>\n    </header>\n    "), HTML.MAIN("\n        ", HTML.Raw("<br>"), "\n        ", Spacebars.include(view.lookupTemplate("notifications")), "\n        ", Spacebars.include(view.lookupTemplate("TabStructure")), "\n\n    ") ];
}));

Template.__checkName("EditAccompanistProfiles");
Template["EditAccompanistProfiles"] = new Template("Template.EditAccompanistProfiles", (function() {
  var view = this;
  return HTML.Raw('<button><a href="/newaccomp">Edit Accompanist Profile</a></button>\n  <button name="requestNotification">Press</button>');
}));

Template.__checkName("TabStructure");
Template["TabStructure"] = new Template("Template.TabStructure", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row"
  }, HTML.Raw('\n    <div class="col s12">\n      <ul class="tabs">\n        <li class="tab col s3"><a href="#test1">Booking Requests</a></li>\n        <li class="tab col s3"><a href="#test2">Profile</a></li>\n        <li class="tab col s3"><a href="#test3">Analytics</a></li>\n        <li class="tab col s3"><a href="#test4">Settings</a></li>\n      </ul>\n    </div>\n    '), HTML.DIV({
    id: "test1",
    "class": "col s12"
  }, Spacebars.include(view.lookupTemplate("CollapsibleStructure"))), "\n    ", HTML.DIV({
    id: "test2",
    "class": "col s12"
  }, Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("isInRole"), "accompanist");
  }, function() {
    return Blaze._TemplateWith(function() {
      return {
        template: Spacebars.call("accompanistProfileTemplate"),
        data: Spacebars.call(view.lookup("myAccompanistProfile"))
      };
    }, function() {
      return Spacebars.include(function() {
        return Spacebars.call(Template.__dynamic);
      });
    });
  })), "\n    ", HTML.DIV({
    id: "test3",
    "class": "col s12"
  }, Spacebars.include(view.lookupTemplate("NewAccompLayout"))), HTML.Raw('\n    <div id="test4" class="col s12">Settings</div>\n  '));
}));

Template.__checkName("CollapsibleStructure");
Template["CollapsibleStructure"] = new Template("Template.CollapsibleStructure", (function() {
  var view = this;
  return Spacebars.With(function() {
    return Spacebars.call(view.lookup("receivedBookingRequests"));
  }, function() {
    return [ "\n    ", HTML.UL({
      "class": "collapsible",
      "data-collapsible": "expandable"
    }, "\n      ", HTML.LI("\n        ", HTML.DIV({
      "class": "collapsible-header active"
    }, HTML.I({
      "class": "material-icons"
    }, "people"), "Pending Bookings (", Blaze.View("lookup:..pending.length", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "pending", "length"));
    }), ")"), "\n        ", HTML.DIV({
      "class": "collapsible-body"
    }, Blaze._TemplateWith(function() {
      return {
        template: Spacebars.call("bookingList"),
        data: Spacebars.call(Spacebars.dot(view.lookup("."), "pending"))
      };
    }, function() {
      return Spacebars.include(function() {
        return Spacebars.call(Template.__dynamic);
      });
    })), "\n      "), "\n\n      ", HTML.LI("\n        ", HTML.DIV({
      "class": "collapsible-header active"
    }, HTML.I({
      "class": "material-icons"
    }, "filter_drama"), "Ongoing Bookings (", Blaze.View("lookup:..ongoing.length", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "ongoing", "length"));
    }), ")"), "\n        ", HTML.DIV({
      "class": "collapsible-body"
    }, Blaze._TemplateWith(function() {
      return {
        template: Spacebars.call("bookingList"),
        data: Spacebars.call(Spacebars.dot(view.lookup("."), "ongoing"))
      };
    }, function() {
      return Spacebars.include(function() {
        return Spacebars.call(Template.__dynamic);
      });
    })), "\n      "), "\n\n      ", HTML.LI("\n        ", HTML.DIV({
      "class": "collapsible-header active"
    }, HTML.I({
      "class": "material-icons"
    }, "filter_drama"), "Previous Bookings (", Blaze.View("lookup:..completed.length", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "completed", "length"));
    }), ")"), "\n        ", HTML.DIV({
      "class": "collapsible-body"
    }, Blaze._TemplateWith(function() {
      return {
        template: Spacebars.call("bookingList"),
        data: Spacebars.call(Spacebars.dot(view.lookup("."), "completed"))
      };
    }, function() {
      return Spacebars.include(function() {
        return Spacebars.call(Template.__dynamic);
      });
    })), "\n      "), "\n\n      ", HTML.LI("\n        ", HTML.DIV({
      "class": "collapsible-header active"
    }, HTML.I({
      "class": "material-icons"
    }, "filter_drama"), "Cancelled Bookings (", Blaze.View("lookup:..cancelled.length", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "cancelled", "length"));
    }), ")"), "\n        ", HTML.DIV({
      "class": "collapsible-body"
    }, Blaze._TemplateWith(function() {
      return {
        template: Spacebars.call("bookingList"),
        data: Spacebars.call(Spacebars.dot(view.lookup("."), "cancelled"))
      };
    }, function() {
      return Spacebars.include(function() {
        return Spacebars.call(Template.__dynamic);
      });
    })), "\n      "), "\n    "), "\n  " ];
  }, function() {
    return "\n  You currently have no bookings... Here's how you can increase your profile.\n  ";
  });
}));

Template.__checkName("request");
Template["request"] = new Template("Template.request", (function() {
  var view = this;
  return HTML.LI({
    "class": "collection-item avatar"
  }, HTML.Raw('\n      <img src="/images/profile.png" alt="" class="circle">\n      '), Spacebars.With(function() {
    return Spacebars.dataMustache(view.lookup("basicProfileById"), view.lookup("musician"));
  }, function() {
    return [ "\n        ", HTML.SPAN({
      "class": "title"
    }, HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("getProfileRoute"), view.lookup("_id"));
      }
    }, Blaze.View("lookup:name", function() {
      return Spacebars.mustache(view.lookup("name"));
    }))), "\n      " ];
  }), "\n      ", Spacebars.With(function() {
    return Spacebars.dataMustache(view.lookup("musicProfileById"), view.lookup("musician"));
  }, function() {
    return [ "\n        ", HTML.P({
      "class": "no-padding"
    }, Blaze.View("lookup:instrument", function() {
      return Spacebars.mustache(view.lookup("instrument"));
    }), " "), "\n      " ];
  }), "\n        ", HTML.P({
    "class": "no-padding"
  }, Blaze.View("lookup:formatDuration", function() {
    return Spacebars.mustache(view.lookup("formatDuration"), Spacebars.dot(view.lookup("."), "startDate"), Spacebars.dot(view.lookup("."), "endDate"));
  })), "\n      ", HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("getBookingRoute"), view.lookup("_id"));
    },
    "class": "secondary-content modal-trigger waves-effect waves-light review-booking"
  }, HTML.Raw('<i class="material-icons">grade</i>')), "\n    ");
}));

Template.__checkName("bookingList");
Template["bookingList"] = new Template("Template.bookingList", (function() {
  var view = this;
  return HTML.UL({
    "class": "collection"
  }, "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("."));
  }, function() {
    return [ "\n     ", Blaze._TemplateWith(function() {
      return {
        template: Spacebars.call("request"),
        data: Spacebars.call(view.lookup("."))
      };
    }, function() {
      return Spacebars.include(function() {
        return Spacebars.call(Template.__dynamic);
      });
    }), "\n    " ];
  }), "\n  ");
}));
