
Template.__checkName("NewAccompLayout");
Template["NewAccompLayout"] = new Template("Template.NewAccompLayout", (function() {
  var view = this;
  return HTML.MAIN("\n  \n    ", Spacebars.include(view.lookupTemplate("NewAccompanist")), "\n        \n    ");
}));

Template.__checkName("NewAccompanist");
