
Template.__checkName("BookingRequestLayout");
Template["BookingRequestLayout"] = new Template("Template.BookingRequestLayout", (function() {
  var view = this;
  return [ HTML.Raw("<header>\n    </header>\n    "), HTML.MAIN("\n      ", Blaze._TemplateWith(function() {
    return {
      template: Spacebars.call("BookingRequest"),
      data: Spacebars.call(view.lookup("routeTransaction"))
    };
  }, function() {
    return Spacebars.include(function() {
      return Spacebars.call(Template.__dynamic);
    });
  }), "\n    ") ];
}));

