Template.__checkName("upsertSessionResponse");
Template["upsertSessionResponse"] = new Template("Template.upsertSessionResponse", (function() {
  var view = this;
  return Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Sessions"),
      id: Spacebars.call("upsertSessionResponse"),
      doc: Spacebars.call(view.lookup("currentResponse")),
      type: Spacebars.call(view.lookup("formType"))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("autoForm"), function() {
      return [ "\n    ", HTML.FIELDSET("\n      ", HTML.LEGEND("Confirm Your First Session"), "\n        ", Blaze._TemplateWith(function() {
        return {
          name: Spacebars.call("startTime"),
          type: Spacebars.call("select-radio"),
          options: Spacebars.call(view.lookup("optionArray"))
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("afFieldInput"));
      }), "\n    "), "\n    ", HTML.BR(), "\n  " ];
    });
  });
}));
