
Template.__checkName("BookingsLayout");
Template["BookingsLayout"] = new Template("Template.BookingsLayout", (function() {
  var view = this;
  return [ HTML.Raw("<header>\n        <h1> Your Bookings </h1>\n    </header>\n    "), HTML.MAIN("\n      ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("sentBookingRequests"));
  }, function() {
    return [ "\n        ", Blaze._TemplateWith(function() {
      return {
        template: Spacebars.call("SentBookingRequests"),
        data: Spacebars.call(view.lookup("."))
      };
    }, function() {
      return Spacebars.include(function() {
        return Spacebars.call(Template.__dynamic);
      });
    }), "\n      " ];
  }), "\n    ") ];
}));

Template.__checkName("SentBookings");
Template["SentBookings"] = new Template("Template.SentBookings", (function() {
  var view = this;
  return [ HTML.Raw("<h2>Booking</h2>\n      "), Spacebars.With(function() {
    return Spacebars.dataMustache(view.lookup("accountById"), view.lookup("accompanist"));
  }, function() {
    return [ "\n      Accompanist: ", Blaze.View("lookup:name", function() {
      return Spacebars.mustache(view.lookup("name"));
    }), HTML.BR(), "\n      " ];
  }), HTML.Raw("\n      Planned Repertoire: <br>\n      "), Blaze.Each(function() {
    return Spacebars.call(view.lookup("repertoire"));
  }, function() {
    return [ "\n        ", Blaze.View("lookup:.", function() {
      return Spacebars.mustache(view.lookup("."));
    }), HTML.BR(), "\n      " ];
  }), "\n      Status: ", Blaze.View("lookup:status", function() {
    return Spacebars.mustache(view.lookup("status"));
  }), HTML.Raw(" <br>\n      Start Date: "), Blaze.View("lookup:startDate", function() {
    return Spacebars.mustache(view.lookup("startDate"));
  }), HTML.Raw(" <br>\n      End Date: "), Blaze.View("lookup:endDate", function() {
    return Spacebars.mustache(view.lookup("endDate"));
  }), HTML.Raw(" <br>") ];
}));
